import React, { useState } from "react";
import firebase from "../config/config";
function New() {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [file, setFile] = useState();
  const [Img, setImg] = useState("");
  const db = firebase.firestore();
  const auth = firebase.auth();
  const storage = firebase.storage();

  const SubmitFun = (e) => {
     e.preventDefault();
firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
      // User is signed in.

      if(user.email == 'idukpaye@gmail.com' ){
       
    let StroageRef = firebase.storage().ref("images/" + file.name);

    let task = StroageRef.put(file);
    task.on(
      "state_changed",
      function progress(snap) {
        let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
        console.log(percentage);
      },

      function error(err) { 
        alert(err.message);
      },

      function done() {
        // const db = firebase.firestore();
        alert("File Uploaded");
        let ImgUrl = StroageRef.getDownloadURL().then((url) => {
          db.collection("Blogs").add({
            title: title,
            body: body,
            user: firebase.auth().currentUser.email,
            ImgUrl: url,
            file:file.name
          });
        });
      }
    );
      
      }
    else {
       alert('You are not authicated to upload a blog ')
setTimeout(function(){
alert('Contact our Admin Team')
  }, 3000);
    }
    } else {

    }
  });
}

  

  return (

    <div className='container'>
      <form onSubmit={SubmitFun}>

        <input
          type="text"
          onChange={(e) => setTitle(e.target.value)}
          id="Blog Title"

          placeholder='Blog Title'
        />

     
        <input
          type="text"
          onChange={(e) => setBody(e.target.value)}
          placeholder="Blog Body" 
        />
        <input type="file" onChange={(e) => setFile(e.target.files[0])} />
        <button type="submit">Submit</button>
      </form>
    </div>
  );
}

export default New;
