import React, { useState, useEffect } from "react";
import firebase from "../config/config";
import Blog from '../Blogs/Blog'
import Navbar from './Navbar'
import { NavLink, Link } from "react-router-dom";
function Home() {  
const [user , setuser]  = useState({email:"None"})
  firebase.auth().onAuthStateChanged(user => {
    if(user){
      setuser(user)
    }
    else{
      window.location.href = '/Login'
    }
  })
  const [User, setUser] = useState("hawk");
  const [blogs , setBlogs] = useState([])
  const db = firebase.firestore()
        useEffect(() => {
db.collection('Blogs').onSnapshot(snapshot => {
  setBlogs(snapshot.docs)
})
  
  });

  return (
    <div> 
<Navbar user={user} />
      <div className="blogs">


      <button onClick={e => firebase.auth().signOut()}>Logout From you Accout</button>
 {
  blogs.map(blog  => {
    return(
<Blog title={blog.data().title} email={blog.data().user}  body={blog.data().body}  img={blog.data().ImgUrl }  key={blog.id}  id={blog.id}/>
     
      )
  })
 }
      </div>
    </div>
  );
}

export default Home;



