import react, { useState, useEffect } from "react";
import { NavLink, Link } from "react-router-dom";
import firebase from "../config/config";
function Each(props) {
    	const  message =  {title:'Blog deleted' , body:"None" , ImgUrl:"abcde"}
let ref =  props.match.params.post_id
const db = firebase.firestore()
const [blogs , setBlogs]= useState([])

	

useEffect( () => {
db.collection('Blogs').doc(ref).onSnapshot(snapshot=> {
setBlogs(snapshot.data())
})
})




const handleClick = e => {
firebase.auth().onAuthStateChanged(function (user) {
    if (user) {
    	
      // User is signed in.
      if(user.email == 'idukpaye@gmail.com'){
      	let imgRef = firebase.storage().ref().child(`images/${blogs.file}`)
		

		imgRef.delete().then(() => {


    	setBlogs(message)
    		db.collection('Blogs').doc(ref).delete().then(() => {
    			setBlogs(message)
    			window.location.href  = '/home'
		} )
	})


	.catch(err => console.log(err))


      }
else{
	alert("Sorry Only admins Can Delete Blogs")
}
    } else {
      
      window.location.href = "/login";
    }
  });
    		
}
  return (
    <div className='Each-user'>
<h4>{blogs.title}</h4>
<img src={blogs.ImgUrl} alt="" />   <button className='red btn' onClick={handleClick}>Delete</button>
    <p>{blogs.body}</p>
    </div>
  )
}

export default Each

